<template>
  <div class="signup-page container py-5 d-flex justify-content-center">
    <div class="col-md-8 col-lg-6">
      <h2 class="text-center mb-4"></h2>
      <form @submit.prevent="handleLogin" class="p-4 border rounded bg-light shadow-sm">
        <!-- Email input -->
        <div class="form-group mb-3">
          <label for="email" class="form-label"></label>
          <input type="email" id="email" v-model="email" class="form-control" required placeholder=" " />
        </div>

        <!-- Password input -->
        <div class="form-group mb-3">
          <label for="password" class="form-label"></label>
          <input type="password" id="password" v-model="password" class="form-control" required
            placeholder=" " />
        </div>

        <!-- Login button -->
        <div class="form-group mb-3">
          <button type="submit" class="btn btn-primary btn-block w-100"></button>
        </div>
      </form>

      <!-- Links for Signup and Forgot Password -->
      <div class="text-center mt-3">
        <p>
           ?
          <router-link to="/signup" class="text-primary fw-bold"></router-link>
        </p>
        <p>
          <router-link to="/forgot-password" class="text-secondary"> </router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "LoginHomePage",
  data() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    ...mapActions(['login']),
    async handleLogin() {
      try {
        const credentials = {
          email: this.email,  // `email`  Spring Boot  
          password: this.password
        };

        console.log("Sending credentials:", credentials); // :   

        // Vuex  login  
        const response = await this.login(credentials);

        //      
        if (response.success) {
          this.$router.push('/');
        } else {
          alert(response.message || ' .');
        }
      } catch (error) {
        //   
        alert(error.message || ' :   .');
      }
    }
  }
};
</script>

<style scoped>
.login-page {
  max-width: 400px;
  margin: 0 auto;
  padding-top: 50px;
}
</style>
